@keyframes blink {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes blink2 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


.header {
  padding: 48px 0 0;
  padding-right: calc((100vw - 320px)/(1440 - 320) * (95 - 16) + 16px);
  padding-left: calc((100vw - 320px)/(1440 - 320) * (95 - 16) + 16px);
  width: calc((100vw - 320px)/(1440 - 320) * (800 - 280) + 280px);
}

.header_wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.nav_container {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.nav_container_hidden {
  display: none;
}


.logo_container {
  border: 2px solid white;
  border-top: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 12px;
  border-top-right-radius: 10px;
  background-color: #FFF;
}

.logo_text {
  color: rgb(79, 211, 145);
  color: #B877FF;
  font-family: 'Unbounded';
  font-size: calc((100vw - 320px)/(1440 - 320) * (32 - 20) + 20px); 
  letter-spacing: -.2rem;
  margin: 0;
  animation: blink 2s forwards;

}

.logo_text_under {
  margin: 0;
  color: #000;
  font-family: 'Unbounded';
  font-size: 24px;
  letter-spacing: -.3rem;
}

.nav_list {
  /* outline: 1px solid #B877FF; */
  width: calc((100vw - 320px)/(1440 - 320) * (800 - 280) + 280px);
  height: calc((100vw - 320px)/(1440 - 320) * (100 - 60) + 60px);
  justify-content: space-between;
  align-items: center;
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav_item {
  /* width: calc((100vw - 320px)/(1440 - 320) * (200 - 70) + 70px); */
  display: flex;
  justify-content: center;
  flex: 1;
}

.link {
  border: 1px solid white;
  flex: 1;
  /* background-color: #ffffff; */
  padding: calc((100vw - 320px)/(1440 - 320) * (37 - 24) + 24px);
  padding-left: calc((100vw - 320px)/(1440 - 320) * (26 - 6) + 6px);
  padding-right: calc((100vw - 320px)/(1440 - 320) * (21 - 6) + 6px);

  /* padding-bottom: calc((100vw - 320px)/(1440 - 320) * (28 - 16) + 16px);
  padding-top: calc((100vw - 320px)/(1440 - 320) * (42 - 16) + 16px); */
  text-decoration: none;
  color: #ffffff;
  font-family: Inter Tight;
  font-family: 'Unbounded';
  font-size: calc((100vw - 320px)/(1440 - 320) * (22 - 10) + 10px);
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  transition: background-color .2s ease-out, border-radius .4s ease-in-out;
  box-sizing: border-box;
}

.link a {
  display: block;

}

.link:hover {
  color: #FFF;
  background-color: #B877FF;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
}

.nav_item a {
  display: block;
}

.nav_item:first-of-type:hover a {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 30px;
  background-color: #B877FF;
} 

.nav_item:last-of-type:hover a {
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 0;
  background-color: #B877FF;
} 

.right_nav_container {
  display: flex;
  align-items: center;
  transition: opacity .2s ease-out;
}

.right_nav_image:hover {
  opacity: .65;
}

.right_nav_image {
  width: 24px;
  height: 24px;
  margin: 0 20px 0 0;
}

.curcle {
  background-color: black;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}


.cart_wrapper {
  position: absolute;
  top: 80px;
  right: 30px;
  z-index: 3;
}

.burger_icon {
  width: 28px;
  height: 28px;
  border: 0;
  overflow: hidden;
  position: relative;
  background-color: transparent;
  transition: transform, top 0.3s ease 0s;
  display: none;
}


.burger_icon_active {

}


.burger_span , .burger_icon::before {
  content: "";
  display: block;
  height: 2px;
  width: 80%;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  position: absolute;
  left: 0;
  transition: all .3s ease-in;
}

.burger_span:hover .burger_icon::before {
  opacity: .7;
}

.burger_span {
  bottom: 30%;
}


.burger_icon::before {
  top: 30%;
}

.burger_icon_active .burger_span {
  top: 50%;
  transform: translate(0px, -50%) rotate(45deg);
}

.burger_icon_active::before {
  top: 50%;
  transform: translate(0px, -50%) rotate(135deg)
}

