.main_container {
  background-color: black;
  height: 90vh;
  padding: 42px 0 62px;
  padding-bottom: calc((100vw - 320px)/(1440 - 320) * (62 - 4) + 4px);
  padding-left: calc((100vw - 320px)/(1440 - 320) * (95 - 30) + 30px);
  padding-right: calc((100vw - 320px)/(1440 - 320) * (95 - 30) + 30px);
}

.title {
  margin-top: calc((100vw - 320px)/(1440 - 320) * (32 - 30) + 30px);
  text-align: center;
  font-family: Unbounded;
  font-size: calc((100vw - 320px)/(1440 - 320) * (55 - 30) + 30px);
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  background: linear-gradient(90deg, rgba(182,38,215,1) 23%, rgba(191,246,233,1) 53%, rgb(75, 225, 255) 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}