@keyframes textShine {
  0% {
      background-position: 0% 50%;
  }
  100% {
      background-position: 100% 50%;
  }
}

.main_container::-webkit-scrollbar {
  width: 6px;
  padding: 100px;
  margin: 100px;
}

.main_container::-webkit-scrollbar-track {
  border-radius: 1px;
  margin: 34px 0;
}

.main_container::-webkit-scrollbar-thumb {
  background: rgb(236, 236, 236);
  border-radius: 40px;
}

.main_container {
  background-color: rgb(0, 0, 0);
  display: flex;
  flex-direction: column;
  gap: 36px;
  height: 70vh; /* Высота 100% окна просмотра */
  overflow-y: auto; /* Включить скролл, если контент не помещается */
  border-radius: 40px;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(255, 255, 255, 0.7);
  -moz-box-shadow: 0px 0px 5px 0px rgba(255, 255, 255, 0.7);
  box-shadow: 0px 0px 5px 0px rgba(255, 255, 255, 0.7);
}

.project_container {
  /* outline: 1px solid white; */
  border-radius: 40px;
  box-shadow: 0 6px 16px rgba(0,0,0,0.1), 0 5px 5px rgba(0,0,0,0.1);

}

.project_container_stellar {
  box-shadow: 0 6px 16px rgba(0,0,0,0.1), 0 5px 5px rgba(0,0,0,0.1);
  background-image: url(https://user-images.githubusercontent.com/61308457/168847872-31664c4c-c94c-4b35-9d98-3cdccf80c1b6.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 6000%;
  backdrop-filter: blur(100px);
}

.project_container_nayzak {
  filter: contrast(1.2);
  background-image: url(../../images/nayzak-blur.jpg);
  background-repeat: no-repeat;
  background-position: bottom;
  backdrop-filter: blur(10px); 
}

.project_container_travel {
  background-image: url(../../images/travel_blur.jpg);
  background-repeat: no-repeat;
  background-position: bottom;
  backdrop-filter: blur(10px); 
  background-size: 100%;
}

.project_container_mesto {
  background-image: url(../../images/mesto_blur.jpg);
  background-repeat: no-repeat;
  background-position: bottom;
  backdrop-filter: blur(10px);
  background-size: 80% 100%;
}

.link {
  text-decoration: none;
}

.links_container {
  display:  flex;
  flex-wrap: wrap;
  gap: 16px;
  padding: 16px;
  justify-content: space-between;
}

.link_item {
  text-decoration: none;
  background: linear-gradient(30deg, rgb(223, 117, 247) 23%, rgba(191,246,233,1) 53%, rgb(75, 225, 255) 100%);
  background-repeat: no-repeat;
  background-size: 100%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 500% auto;
  font-size: calc((100vw - 320px)/(1440 - 320) * (32 - 22) + 22px);
  text-align: center;
  font-style: normal;
  font-weight: 500;
  transition: color .3s ease-in ;
  animation: textShine 3.5s ease-in-out infinite alternate;
}

.project_score svg, .stuff_item svg {
  min-width: 16px;
  min-height: 16px;
  width: calc((100vw - 320px)/(1440 - 320) * (34 - 24) + 24px);
  height: calc((100vw - 320px)/(1440 - 320) * (34 - 24) + 24px);
}

.link_item:hover {
  color: palevioletred;
} 

.project_item {
  width: calc((100vw - 320px)/(1440 - 320) * (400 - 280) + 280px);
  width: 100%;
  border-radius: 40px;
  display: flex; 
  flex-flow: column;
  padding-left: calc((100vw - 320px)/(1440 - 320) * (32 - 6) + 6px);
  padding-right: calc((100vw - 320px)/(1440 - 320) * (32 - 8) + 8px);
  justify-content: space-between;
  box-sizing: border-box;
}

.project_name {
  border-bottom-left-radius: 40px;
  border-bottom-right-radius: 40px;
  margin: 0 auto;
  overflow: hidden;
  font-family: "Roboto";
  background-color: rgb(255, 255, 255);
  line-height: 120%;
  font-size: calc((100vw - 320px)/(1440 - 320) * (44 - 24) + 24px);
  text-align: center;
  font-style: normal;
  font-weight: 500;
  letter-spacing: -0.6px;
  color: rgb(0, 0, 0);
  padding: 0 22px;
  padding-left: calc((100vw - 320px)/(1440 - 320) * (22 - 14) + 14px);
  padding-right: calc((100vw - 320px)/(1440 - 320) * (22 - 14) + 14px);
}

.project_name_nazdak {
  color: rgb(255, 255, 255);
  background-color: rgb(0, 0, 0);
}

.project_name_smt {
  color: rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
}


.project_item_stellar {
