  background-repeat: no-repeat;
  background-position: center; */
  /* background-size: 3300px; */
}

.project_item_nayzak {
  filter: contrast(1.2);
  background-image: url(../../images/nayzak.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 2000px 2000px;
}

.project_item_mesto {
  background-image: url(https://user-images.githubusercontent.com/92729800/204303554-fd708625-c724-46f9-9045-261b69f63b27.png);
  background-repeat: no-repeat;
  background-size: 2000px 2000px;
  filter: blur(100px);
}

.project_item_smt {
  background-image: url(../../images/travel2.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.wrapper {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  align-items: flex-start;
  gap: 12px;
  padding: 6px 0;
  padding-left: calc((100vw - 320px)/(1440 - 320) * (16 - 16) + 16px);
  padding-right: calc((100vw - 320px)/(1440 - 320) * (16 - 8) + 8px);
  margin-bottom: 20px;
  margin-top: 50px;
  margin-right: 10px;
  max-width: 280px;
  min-width: none;
  position: relative;
  
  box-sizing: border-box;

}

.wrapper::before {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  z-index: -1;
  background-color: rgb(0, 0, 0);
  border-radius: 40px;
  -webkit-box-shadow: 0px 0px 5px 1px rgba(34, 60, 80, 0.6) ;
  -moz-box-shadow: 0px 0px 5px 1px rgba(34, 60, 80, 0.6) ;
  box-shadow: 0px 0px 5px 1px rgba(34, 60, 80, 0.6) ;
}

.wrapper_stellar:before {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  z-index: -1;
  background-color: rgb(255, 255, 255);
  border-radius: 40px;
}

.stuffs_container {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  padding: 0;
  margin: 0;
}

.title {
  font-family: "Roboto";
  font-size: calc((100vw - 320px)/(1440 - 320) * (28 - 22) + 22px);
  font-style: normal;
  font-weight: 500;
  line-height: 1.25;
  letter-spacing: -0.6px;
  color: rgb(0, 0, 0);
  margin: 0 0 12px;
}

.title_nayzak {
  color: white;
}

.title_mesto {
  color: white;
 }

.title_smt {
  color: black;
}

.description_container {
  background-color: #000000;
  padding: 0 32px;
  border-radius: 0 0 40px 40px;
}

.description {
  padding: 16px;
  margin: 0;
  font-family: "Roboto";
  font-size: calc((100vw - 320px)/(1440 - 320) * (22 - 16) + 16px);
  font-style: normal;
  font-weight: 500;
  letter-spacing: -0.6px;
  color: rgb(255, 255, 255);
}

.feature_title {
  margin: 14px 0 14px;
  margin-left: calc((100vw - 320px)/(1440 - 320) * (16 - 0) + 0px);
  font-family: Unbounded;
  background: linear-gradient(190deg, rgba(191,246,233,1) 53%, rgb(75, 225, 255) 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  line-height: 120%;
  font-size: calc((100vw - 320px)/(1440 - 320) * (22 - 16) + 16px);
  font-style: normal;
  font-weight: 400;
  word-wrap: break-word;
  min-width: 80px;
}

.features_container {
  display: flex;
  flex-direction: column;
  gap: 6px;
  padding: 0 16px 16px 35px;
  padding-left: calc((100vw - 320px)/(1440 - 320) * (36 - 8) + 8px);
  padding-right: calc((100vw - 320px)/(1440 - 320) * (16 - 8) + 8px);
  margin: 0;
}

.feature_item {
  color: rgb(255, 255, 255);
  margin: 0;
  font-family: "Roboto";
  font-size: calc((100vw - 320px)/(1440 - 320) * (22 - 16) + 16px);
  font-style: normal;
  font-weight: 300;
  letter-spacing: -0.6px;
}

.feature_item::marker {
  color: rgb(75, 225, 255);
}

.wrapper_smt:before {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  z-index: -1;
  background-color: rgb(255, 255, 255);
  border-radius: 40px;
}