@keyframes blink {
  0% {
    filter: hue-rotate(0deg);
  }
  50% {
    
    filter: hue-rotate(220deg);
    
  }
  100% {
    filter: hue-rotate(0deg);
  }
}

@keyframes appearing {
  0% {
    filter: blur(10px);
  }
  100% {
    
    filter: blur(0);
    
  }
}


