@keyframes textShine {
  0% {
      background-position: 30% 50%;
  }
  100% {
      background-position: 100% 50%;
  }
}

.main_container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 8px;
  background-color: rgb(0, 0, 0);
  padding-top: calc((100vw - 320px)/(1440 - 320) * (38 - 38) + 38px);
  padding-bottom: calc((100vw - 320px)/(1440 - 320) * (32 - 16) + 16px);
  padding-right: calc((100vw - 320px)/(1440 - 320) * (95 - 8) + 8px);
  padding-left: calc((100vw - 320px)/(1440 - 320) * (95 - 8) + 8px);
  box-sizing: border-box;
  
}

.avatar_container {
  background-image: url(../../img/hello_photo.png);
  background-repeat: no-repeat;
  background-size: 100vw;
  border-radius: 50%;
  filter: contrast(1.5);
  width: 28vw;
  animation: textShine 15s ease-in-out infinite alternate;
}

.avatar_img {
  display: block;
  width: 28vw;
}

.icons_container {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: calc((100vw - 320px)/(1440 - 320) * (24 - 10) + 10px);
}

.title {
  font-family: "Roboto";
  font-size: 28px;
  font-style: normal;
  font-weight: 300;
  line-height: 38px;
  letter-spacing: -0.6px;
  color: rgb(0, 0, 0);
  margin: 0 0 12px;
  text-align: center;
}

.icons_container_item {
  width: calc((100vw - 320px)/(1440 - 320) * (500 - 200) + 200px);
  display: flex;
  align-items: center;
  gap: 12px;
  font-family: "Roboto";
  font-size: calc((100vw - 320px)/(1440 - 320) * (24 - 16) + 16px);
  font-style: normal;
  font-weight: 300;
  line-height: 38px;
  letter-spacing: -0.6px;
  color: rgb(255, 255, 255);
  text-decoration: none;
  box-sizing: border-box;
}

.icons_container_item svg {
  min-width: 20px;
  min-height: 20px;
  width: calc((100vw - 320px)/(1440 - 320) * (42 - 24) + 20px);
  height: calc((100vw - 320px)/(1440 - 320) * (42 - 24) + 20px);
}

.icons_container_item a {
  padding: 0;
}

.icons_container_item_telegram:hover {
 color: rgba(13, 110, 253, 1)
}

.icons_container_item_git:hover {
  color: #faa356
 }

.icons_container_item_phone, .icons_container_item_git, .icons_container_item_telegram, .icons_container_item {
  transition: color .2s ease-out;
}

.icons_container_item_phone:hover {
  color: rgb(69, 233, 44)
}

.router_icon {
  width: 142px;
  height: 75px;
  /* object-fit: cover; */
  color: rgb(255, 255, 0);
}