.wrapper_container {
  background-image: url(../../img/hello_photo.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  /* max-height: 700px; */
  height: calc((100vw - 320px)/(1440 - 320) * (700 - 500) + 500px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.main_container {
  padding: 0 95px;
  padding-right: calc((100vw - 320px)/(1440 - 320) * (95 - 16) + 16px);
  padding-left: calc((100vw - 320px)/(1440 - 320) * (95 - 16) + 16px);
  top: 30%;
  display: flex;
  justify-content: space-between ;
}

.tilda_container {

}

.tilda {
  font-family: 'Unbounded';
  font-size: 20px;
  letter-spacing: -.3rem;
  line-height: 1;
  color: white;
  width: calc((100vw - 320px)/(1440 - 320) * (240 - 115) + 115px);
  overflow: hidden;
  /* background-image: linear-gradient(to right, rgb(17, 225, 252) -4.01%, #F983E9 36.14%, #B877FF 77.44%, #C2E9CD 106.11%); 
  animation: textShine 5s ease-in-out infinite alternate;
  animation: snake 5s ease-in-out infinite alternate;
  background-repeat: no-repeat;
  background-size: 100%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 500% auto; */
  margin: 0;
  transform: scaleY(1);
}

.description_container {
}

.slogan {
  margin: 0;
  font-family: 'Unbounded';
  font-size: calc((100vw - 320px)/(1440 - 320) * (50 - 27) + 27px);
  line-height: 1.3;
  font-weight: 300;
  background-image: linear-gradient(91.17deg, #C6F1F7 -4.01%, #F983E9 36.14%, #B877FF 77.44%, #C2E9CD 106.11%); 
  background-repeat: no-repeat;
  background-size: 100%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-fill-color: transparent;
  background-size: 500% auto;
  animation: textShine 5s ease-in-out infinite alternate;
}

.slogan_description {
  margin: 12px 0 0;
  color: #FFF;
  font-family: Unbounded;
  font-size: calc((100vw - 320px)/(1440 - 320) * (16 - 12) + 12px);
  font-style: normal;
  font-weight: 200;
  line-height: normal;
}

.second_column {
  align-self: center;
}

.second_column_text {
  font-family: Inter Tight;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  background: linear-gradient(91deg, #C6F1F7 -4.01%, #F983E9 36.14%, #B877FF 77.44%, #C2E9CD 106.11%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  max-width: 216px;
}

.job_field_container {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-right: calc((100vw - 320px)/(1440 - 320) * (95 - 16) + 16px);
  padding-left: calc((100vw - 320px)/(1440 - 320) * (95 - 16) + 16px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  background-color: rgba(0, 0, 0, 0.6);
  height: 12vh;
  width: 100vw;
  max-width: 2560px;
  box-sizing: border-box;
  align-self: flex-end;
}

.job_field_item {
  font-family: Unbounded;
  font-size: calc((100vw - 320px)/(1440 - 320) * (18 - 11) + 11px);
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  background: linear-gradient(91deg, #C6F1F7 -4.01%, #F983E9 36.14%, #B877FF 77.44%, #C2E9CD 106.11%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  border-bottom: 1px solid white;
  padding-bottom: 8px;
}
