.icons_container::-webkit-scrollbar {
  all:unset;
}

.icons_container::-webkit-scrollbar-track {
  all:unset;
}

.icons_container::-webkit-scrollbar-thumb {
  all:unset;
}

.main_container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 8px;
  background-image: url(../../img/hello_photo.png);
  background-size: 140%;
  padding: 32px 95px;
  background-repeat: no-repeat;
  height: calc((100vw - 320px)/(1440 - 320) * (340 - 250) + 250px);
  width: 100;
  /* 250 430*/
  box-sizing: border-box;
}


.icons_container {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 16px;
  justify-content: space-between;
  width: 90vw;
  overflow-x: scroll;
  padding-bottom: 10px;
}


.icons_container::-webkit-scrollbar {
  width: 1px;
  padding: 0 0 20px;
  height: 4px;
}

.icons_container::-webkit-scrollbar-track {
  border-radius: 1px;
  padding: 20px;
  height: 4px;
}

.icons_container::-webkit-scrollbar-thumb {
  background: rgba(221, 221, 221,.2);
  border-radius: 10px;
  height: 4px;
  padding: 20px;
}

.title {
  font-family: 'Unbounded';
  font-size: 34px;
  font-style: normal;
  font-weight: 500;
  line-height: 38px;
  letter-spacing: -0.6px;
  text-align: center;
  color: aliceblue;
}

.icons_container_item {
  width: calc((100vw - 320px)/(1440 - 320) * (200 - 120) + 120px);
  display: flex;
  align-items: center;
  text-align: center;
  gap: 12px;
  font-family: "Roboto";
  font-size: calc((100vw - 320px)/(1440 - 320) * (28 - 18) + 18px);
  font-style: normal;
  font-weight: 400;
  line-height: 38px;
  letter-spacing: -0.6px;
  background-color: aliceblue;
  border-radius: 40px;
  padding: 0 12px 0 16px;
  padding-top: calc((100vw - 320px)/(1440 - 320) * (10 - 6) + 6px);
  padding-bottom: calc((100vw - 320px)/(1440 - 320) * (10 - 6) + 6px);
  transition: all .3s ease-in-out;
  max-height: 80px;
}

.icons_container_item svg {
  min-width: 24px;
  min-height: 24px;
  width: calc((100vw - 320px)/(1440 - 320) * (42 - 24) + 24px);
  height: calc((100vw - 320px)/(1440 - 320) * (42 - 24) + 24px);
}

.icons_container_item:hover {
  background-color: #F983E9;
  color: white;
}

