@keyframes rotate360 {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.main_container {
  background-color: black;
  /* 530 700 */
  height: calc((100vw - 320px)/(1440 - 320) * (590 - 460) + 460px);
  padding-top: calc((100vw - 320px)/(1440 - 320) * (42 - 30) + 30px);
  padding-bottom: calc((100vw - 320px)/(1440 - 320) * (28 - 28) + 28px);
  padding-right: calc((100vw - 320px)/(1440 - 320) * (95 - 16) + 16px);
  padding-left: calc((100vw - 320px)/(1440 - 320) * (95 - 16) + 16px);
  position: relative;
}

.first_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 32px;
  z-index: 2;
}

.first_row_title {
  margin: 0;
  color: #FFF;
  font-family: Unbounded;
  font-size: calc((100vw - 320px)/(1440 - 320) * (24 - 16) + 16px);
  font-weight: 400;
  line-height: normal;
  background: linear-gradient(91deg, #C6F1F7 -4.01%, #F983E9 36.14%, #B877FF 77.44%, #C2E9CD 106.11%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.features_container {
  padding: 0;
  margin: 24px 0 0;
  list-style: none;
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  gap: 12px;
}

.feature_item {
  font-family: Unbounded;
  color:aliceblue;
  font-size: calc((100vw - 320px)/(1440 - 320) * (22 - 12) + 12px);
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  transition: opacity 5s ease;
}

/* .feature_item__hidden {
  opacity: 0;
} */

.second_row_title {
  font-family: Unbounded;
  font-size: calc((100vw - 320px)/(1440 - 320) * (50 - 26) + 26px);
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  background: linear-gradient(91deg, #C6F1F7 -4.01%, #F983E9 36.14%, #B877FF 77.44%, #C2E9CD 106.11%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: transform 1s ease;
}

/* .second_row_title__hidden {
  transform: translateX(-220px);
} */

.third_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  gap: 32px;

}

.third_row_title {
  max-width: 500px;
  font-family: Unbounded;
  color: aliceblue;
  font-size: calc((100vw - 320px)/(1440 - 320) * (20 - 14) + 14px);
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.third_row_acident__react {
  color: #B877FF;
}

.third_row_acident__js {
  color: yellow;
}


.react_wrapper {

}

.react_wrapper svg {
  /* filter: blur(16px); */
  color: #B877FF !important;
  animation: rotate360 16s linear infinite; 
  width: calc((100vw - 320px)/(1440 - 320) * (160 - 64) + 64px);
  height: calc((100vw - 320px)/(1440 - 320) * (160 - 64) + 64px);
}
